.carrito-page {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Contenedor principal del carrito */
  .carrito-container {
    background: #ffffff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    margin-bottom: 20px;
    transition: transform 0.3s ease;
  }
  
  .carrito-container:hover {
    transform: scale(1.01);
  }
  
  .carrito-page h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 28px;
    color: #333;
  }
  
  /* Estilos para la tabla */
  .cart-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  
  .cart-table thead {
    background: linear-gradient(to right, #2c3e50, #4ca1af);
    color: #fff;
  }
  
  .cart-table th, .cart-table td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #eee;
  }
  
  .cart-table tr:hover {
    background: #f9f9f9;
  }
  
  /* Sección total y botón comprar */
  .carrito-total {
    background: #fafafa;
    padding: 20px;
    border-radius: 15px;
    text-align: center;
  }
  
  .carrito-total p {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .payment-methods {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .payment-methods p {
    margin-right: 10px;
    font-weight: bold;
  }
  
  .payment-methods img {
    margin: 0 5px;
    filter: drop-shadow(0px 2px 2px rgba(0,0,0,0.2));
  }
  
  /* Botón comprar */
  .carrito-total button {
    background: linear-gradient(to right, #4ca1af, #2c3e50);
    color: #fff;
    border: none;
    padding: 12px 25px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 10px;
    transition: background 0.3s ease;
  }
  
  .carrito-total button:hover {
    background: linear-gradient(to right, #2c3e50, #4ca1af);
  }
  
  /* Ajustes responsivos */
  @media (max-width: 600px) {
    .cart-table th, .cart-table td {
      padding: 10px;
      font-size: 14px;
    }
  
    .carrito-total p {
      font-size: 20px;
    }
  
    .carrito-total button {
      font-size: 16px;
      padding: 10px 20px;
    }
  }
  