body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f2f2f2;
  color: #333;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-main {
  flex: 1;
  padding: 20px;
}

/* HEADER */
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right, #2c3e50, #4ca1af);
  padding: 10px 20px;
}

.header-logo a {
  color: #fff;
  font-size: 24px;
  text-decoration: none;
  font-weight: bold;
}

.header-nav a {
  color: #fff;
  text-decoration: none;
  margin-left: 20px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.header-nav a:hover {
  color: #fffa90;
}

.whatsapp-logo {
  width: 24px;
  vertical-align: middle;
  margin-left: 20px;
}

/* FOOTER */
.main-footer {
  background: #2c3e50;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
}

/* INICIO */
.banner-container {
  text-align: center;
  margin-bottom: 20px;
}
.banner-image {
  max-width: 100%;
  height: auto;
}
.sobre-nosotros {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
}

/* CATALOGO */
.catalogo-page .product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 20px;
}

/* PRODUCT CARD */
.product-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  text-align: center;
  padding: 15px;
  transition: transform 0.3s ease;
}
.product-card:hover {
  transform: translateY(-5px);
}
.card-img-container {
  max-width: 100%;
  height: 150px;
  overflow: hidden;
}
.card-img-container img {
  width: 100%;
  height: auto;
}
.product-card h3 {
  margin: 10px 0;
}
.product-card button {
  background: #4ca1af;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}
.product-card button:hover {
  background: #2c3e50;
}

/* CARRITO */
.cart-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
}

.cart-table th, .cart-table td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  text-align: left;
}

.carrito-total {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
}
.carrito-total p {
  font-size: 20px;
  margin-bottom: 10px;
}
.carrito-total button {
  background: #4ca1af;
  color: #fff;
  padding: 10px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
}
.carrito-total button:hover {
  background: #2c3e50;
}

/* INICIAR SESION */
.iniciar-sesion-page {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  max-width: 400px;
  margin: 0 auto;
}

.login-form label {
  display: block;
  margin-bottom: 10px;
}
.login-form input {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
}

.login-form button {
  width: 100%;
  margin-top: 20px;
  background: #4ca1af;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}
.login-form button:hover {
  background: #2c3e50;
}

.switch-mode {
  text-align: center;
  margin-top: 20px;
}
.switch-mode button {
  background: none;
  border: none;
  color: #4ca1af;
  cursor: pointer;
  font-weight: bold;
}

/* CONTACT */
.contact-page {
  max-width: 500px;
  margin: 0 auto;
}
.contact-form-container {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
}
.contact-form-container form label {
  display: block;
  margin-bottom: 10px;
}
.contact-form-container form input,
.contact-form-container form textarea {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
}
.contact-form-container form button {
  margin-top: 20px;
  background: #4ca1af;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}
.contact-form-container form button:hover {
  background: #2c3e50;
}
