.contact-page {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Contenedor principal del formulario */
  .contact-form-container {
    background: linear-gradient(to bottom right, #ffffff, #f0f8ff);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    transition: transform 0.3s ease;
  }
  
  .contact-form-container:hover {
    transform: scale(1.01);
  }
  
  .contact-page h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 28px;
    color: #333;
    font-weight: bold;
  }
  
  /* Estilizando el formulario para que luzca ordenado como en una tabla */
  .contact-form-container form {
    display: table;
    width: 100%;
  }
  
  .contact-form-container form label {
    display: table-row;
    margin-bottom: 15px;
  }
  
  .contact-form-container form label > span {
    display: table-cell;
    width: 150px;
    font-weight: bold;
    vertical-align: middle;
    padding-right: 10px;
    color: #333;
  }
  
  .contact-form-container form input,
  .contact-form-container form textarea {
    display: table-cell;
    width: 100%;
    padding: 8px 10px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 8px;
    background: #fff;
    box-sizing: border-box;
    font-size: 16px;
    color: #333;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .contact-form-container form input:focus,
  .contact-form-container form textarea:focus {
    border-color: #4ca1af;
    box-shadow: 0 0 5px rgba(76,161,175,0.5);
    outline: none;
  }
  
  .contact-form-container form textarea {
    height: 100px;
    resize: vertical;
  }
  
  /* Botón de enviar */
  .contact-form-container form button {
    display: inline-block;
    background: linear-gradient(to right, #4ca1af, #2c3e50);
    color: #fff;
    border: none;
    padding: 12px 25px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 10px;
    margin-top: 20px;
    float: right;
    transition: background 0.3s ease, transform 0.3s ease;
  }
  
  .contact-form-container form button:hover {
    background: linear-gradient(to right, #2c3e50, #4ca1af);
    transform: translateY(-2px);
  }
  
  /* Responsivo */
  @media (max-width: 600px) {
    .contact-form-container form {
      display: block;
    }
    
    .contact-form-container form label > span {
      display: block;
      width: 100%;
      margin-bottom: 5px;
    }
    
    .contact-form-container form button {
      width: 100%;
      text-align: center;
      float: none;
    }
  }
  